html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  color: #333333;
  margin: 0;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

a {
  color: #509EE3;
  text-decoration: none;
  transition: all 0.5s ease-out;
}

img {
  max-width: 100%;
  height: auto;
}

/* Utilidades */
.box-shadow-1 {
  box-shadow: 0.25rem 0.25rem 1rem rgba(0, 0, 0, 0.25);
}

.section {
  padding: 2rem 1rem;
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: var(--max-width);
}

.none {
  display: none;
}

.text-top {
  align-self: flex-start;
  margin-top: 1.25rem;
}

.deshabilitado {
  pointer-events: none;
  opacity: 0.5;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  font-size: 2rem;
  line-height: 1.6;
}

.nombre-pagina {
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
  margin-bottom: 5rem;
}

.contenido-app {
  background-color: #14192D;
}
@media (min-width: 768px) {
  .contenido-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenido-app .imagen {
  height: 20rem;
  background-image: url("../img/background-image.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 768px) {
  .contenido-app .imagen {
    height: auto;
  }
}
.contenido-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
.contenido-app .app h1,
.contenido-app .app p,
.contenido-app .app label {
  color: #FFFFFF;
}

.panel-admin {
  display: grid;
  grid-template-columns: 15% 85%;
  grid-template-areas: "header header" "navegacion panel";
}
.panel-admin h2 {
  color: #222222;
}
.panel-admin .nro-habitacion {
  padding-left: 2.5rem;
  text-transform: uppercase;
}

.fomulario {
  width: 100%;
}

.campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.campo label,
.campo input {
  flex: 0 0 12rem;
  font-size: 2rem;
}
.campo input,
.campo select,
.campo textarea {
  flex: 1;
  padding: 1.25rem;
  border-radius: 1rem;
  border: none;
  outline: none;
  resize: none;
  font-size: 2rem;
}

.boton {
  background-color: #008080;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 2rem 0 5rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: rgba(0, 128, 128, 0.4588235294);
  cursor: pointer;
}

/* Componentes boton animacion icono desplazado */
.boton-crear {
  background-color: #008080;
  color: #FFFFFF;
  padding: 1rem 0;
  font-size: 1.7rem;
  border: none;
  border-radius: 0.7em;
  letter-spacing: 0.08em;
  position: relative;
  display: inline;
  align-content: center;
  align-items: center;
  overflow: hidden;
  padding-left: 2.8em;
  padding-right: 0.9em;
  width: 60%;
}
.boton-crear .icon {
  background-color: #14192D;
  height: 2em;
  width: 2em;
  border-radius: 2em;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 0.4em;
  padding: 0.5rem;
  transition: all 0.5s;
  cursor: pointer;
}
.boton-crear path {
  margin-left: 0.4em;
  transition: all 0.5s;
  width: 1.2rem;
  height: 1.2rem;
  fill: #FFFFFF;
}
.boton-crear:hover .icon svg {
  transform: rotate(360deg);
}
.boton-crear:hover .icon {
  width: calc(100% - 0.9em);
  border-radius: 0.5em;
}

.boton-actualizar {
  background-color: #14192D;
  color: #FFFFFF;
  padding: 1rem 0;
  font-size: 1.7rem;
  border: none;
  border-radius: 0.7em;
  letter-spacing: 0.08em;
  position: relative;
  display: inline;
  align-content: center;
  align-items: center;
  overflow: hidden;
  padding-left: 2.8em;
  padding-right: 0.9em;
  width: 60%;
}
.boton-actualizar .icon {
  background-color: #FFFFFF;
  height: 2em;
  width: 2em;
  border-radius: 2em;
  position: absolute;
  display: flex;
  align-items: center;
