  width: 1.2rem;
  height: 1.2rem;
  fill: #333333;
}
.boton-actualizar:hover .icon svg {
  transform: rotate(360deg);
}
.boton-actualizar:hover .icon {
  width: calc(100% - 0.9em);
  border-radius: 0.5em;
}

/* Componente boton volver con flechita */
.boton-volver {
  display: flex;
  height: 3em;
  width: 12rem;
  margin: 2.5rem 0;
  margin-left: 1.25rem;
  padding: 1rem;
  align-items: center;
  justify-content: center;
  background-color: #CD5C5C;
  color: #FFFFFF;
  border-radius: 0.3rem;
  letter-spacing: 0.1rem;
  transition: all 0.2s linear;
  cursor: pointer;
  border: none;
}
.boton-volver > svg {
  width: 3.2rem;
  height: 3.2rem;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
  font-size: 2rem;
  fill: #FFFFFF;
  transition: all 0.4s ease-in;
}
.boton-volver:hover > svg {
  font-size: 1.2em;
  transform: translateX(-5px);
}
.boton-volver:hover {
  box-shadow: 9px 9px 33px #d1d1d1, -9px -9px 33px #ffffff;
  transform: translateY(-2px);
}

