.acciones {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  justify-content: space-between;
  margin-top: 2.5rem;
}
@media (min-width: 768px) {
  .acciones {
    flex-direction: row;
  }
}
.acciones a {
  text-align: center;
  font-size: 2rem;
}
.acciones a:hover {
  opacity: 1;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #008080;
  height: 6.5rem;
  padding: 2rem;
  color: #FFFFFF;
  grid-area: header;
}

.logo h1 {
  text-transform: uppercase;
  font-weight: 300;
  margin: 0;
}
.logo h1 span {
  font-weight: 700;
}

.barra {
  display: flex;
  align-items: center;
  gap: 5rem;
  font-size: 2rem;
}
.barra a {
  font-weight: 700;
}
.barra a:hover {
  color: #FFFFFF;
}

.menu {
  background-color: #14192D;
  color: #FFFFFF;
  min-height: 100vh;
}
.menu p {
  background-color: #222222;
  padding: 1rem;
  text-align: center;
  font-weight: 700;
}
.menu p,
.menu summary {
  font-size: 1.5rem;
}

.navegacion details {
  padding: 1rem 2rem;
}
.navegacion summary {
  display: flex;
  align-items: center;
  gap: 2.5rem;
  transition: opacity 0.5s ease-out;
}
.navegacion summary:hover {
  opacity: 0.7;
  cursor: pointer;
}

.opciones {
  display: flex;
  flex-direction: column;
}
.opciones a {
  display: inherit;
  align-items: center;
  gap: 2.5rem;
  color: #FFFFFF;
  padding: 0.5rem;
  margin-left: 1rem;
  transition: color 0.5s ease-out;
}
.opciones a:hover {
  color: #509EE3;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

.formulario-crud {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2.5rem;
  padding: 2.5rem;
}
.formulario-crud label {
  font-weight: 700;
  text-transform: uppercase;
  font-size: initial;
}
.formulario-crud input,
.formulario-crud select,
.formulario-crud textarea {
  border: thin solid #CCCCCC;
}
.formulario-crud .campo:last-of-type,
.formulario-crud .campo:nth-child(5) {
  grid-column: span 2;
}

table.tabla {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.tabla thead th {
  color: #FFFFFF;
  padding: 2rem;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 1.8rem;
}
table.tabla tbody {
  text-align: center;
}
table.tabla tbody tr,
table.tabla tbody td {
  border-bottom: thin solid #CCCCCC;
}
table.tabla tbody td {
  padding: 1rem;
  padding-right: 0;
  font-size: 1.9rem;
}
table.tabla a,
table.tabla button {
  display: block;
  width: 100%;
  margin-bottom: 1rem;
}

.tabla-habitaciones thead {
  background-color: rgba(20, 25, 45, 0.4588235294);
}

.tabla-usuarios thead {
  background-color: rgba(0, 128, 128, 0.4588235294);
}

.modal {
  width: 40% !important;
  padding: 2.5rem !important;
}
.modal .modal-icon {
  width: 6rem !important;
  height: 6rem !important;
}
.modal .modal-title {
  color: #14192D;
}
.modal .modal-warning {
  font-size: 1.5rem;
  color: #333333;
}

.modal-btns {
  width: 100%;
  gap: 2rem;
}
.modal-btns .modal-btn-confirm,
.modal-btns .modal-btn-cancel {
  padding: 1rem 1.5rem;
  font-size: 1.5rem !important;
  font-weight: 700;
}
.modal-btns .modal-btn-confirm {
  background-color: #008080 !important;
}
.modal-btns .modal-btn-cancel {
  background-color: #CD5C5C !important;
}